<template>
    <div class="account-name-doEdit">
        <mt-cell title="取款账号:" v-model="infoData.account"></mt-cell>
        <mt-cell title="取款编号:" v-model="infoData.no"></mt-cell>
        <mt-cell title="预约金额:" v-model="infoData.money"></mt-cell>
        <mt-cell title="预约日期:" v-model="infoData.date"></mt-cell>
        <mt-cell title="预约状态:">
            {{infoData.status === true ? '未使用' : '已失效'}}
        </mt-cell>
        <mt-cell title="温馨提示:" v-model="infoData.info" type="textarea"></mt-cell>

        <div class="field-action">
            <mt-button type="primary" size="small">返回</mt-button>
            <mt-button type="primary" size="small" v-if="infoData.status">取消预约</mt-button>
        </div>

    </div>
    </div>
</template>

<script>
    export default {
        name: 'noCard-query-list',
        data() {
            return {
                infoData: {}
            };
        },
        created() {
            let ls = this.$ls.getItem('no-card-list');
            let data = ls.filter(item => {
                return item.id === this.$route.params.id;
            });
            this.infoData = data[0];
            this.infoData.info = '仅显示当日及未使用预约记录，最多20条记录';
        },
        methods: {},
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('no-card-account');
                this.$ls.removeItem('no-card-list');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .cell-top {
        margin: 0 20px;
        padding: 20px 0 0 0;
        font-size: 12px;
        font-weight: 600;
    }
</style>


