import ajax from './ajax';
import api from './api';

/*  查询无卡取款列表 */
export function queryNoCardList(params) {
    let id = params.id;
    return new Promise(function (resolve, reject) {
        ajax.post(api.queryNoCardList, params).then((res) => {
            let list = res.data.filter((item) => {
                if (item.account_code === id) {
                    return item;
                }
            });
            resolve(list);
        }, (res) => {
            reject(res);
        }).catch((err) => {
            reject(err);
        });
    });
}


