<template>
    <div>
        <div class="field-part">
            <mt-cell title="支付账户" @click.native="showAccount()" v-model="dataForm.account" isLink></mt-cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>
        <div class="field-action">
            <mt-button type="primary" size="small">返回</mt-button>
            <mt-button type="primary" size="small" @click="query">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/no-card';
    export default {
        data() {
            return {
                dataForm: {
                    account: '',
                    id: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: '0089',
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            bankName: '安徽农金',
                            balance: '200000.66'
                        },
                        {
                            id: '0025',
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            bankName: '安徽农金',
                            balance: '5000.27'
                        },
                        {
                            id: '0109',
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金',
                            balance: '600.34'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('no-card-account');
            if (ls !== null && ls !== undefined) {
                this.dataForm.account = ls;
            }
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.dataForm.account = this.selectorPaymentAccount.checked.account;
                    this.dataForm.id = this.selectorPaymentAccount.checked.id;
                }, (data) => {
                });
            },
            query() {
                let _data = Object.assign({}, this.dataForm);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('请选择支付账号');
                    return false;
                }
                let params = {
                    id: this.dataForm.id
                };
                services.queryNoCardList(params)
                    .then(res => {
                        this.$ls.setItem('no-card-list', res);
                        this.$router.push({ name: 'noCardQueryList' });
                    });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('no-card-account', this.dataForm.account);
            } else {
                this.$ls.removeItem('no-card-account');
            }
            next(true);
        }
    };

</script>


