<template>
    <div class="noCard-query-list">
        <div class="cell-top">
            {{title}}
        </div>
        <div class="field-part">
            <div v-if="dataList.length !== 0">
                <mt-cell v-for="item in dataList" :key="item.id" @click.native="detail(item)" :title="`${item.account} / ${item.money}元 / ${item.status}`"
                    is-link></mt-cell>
            </div>
            <div class="no-data" v-else>
                <p>
                    {{error}}
                </p>
            </div>
        </div>
        <div class="field-action">
            <mt-button type="primary" size="large">返回</mt-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'noCard-query-list',
        data() {
            return {
                dataList: '',
                error: '未找到对应的数据记录【详细错误原因：查询当天无对应数据！】'
            };
        },
        computed: {
            title() {
                return this.dataList.length === 0 ? '提示信息' : '请选择';
            }
        },
        created() {
            let ls = this.$ls.getItem('no-card-list');
            this.dataList = ls;
            this.dataList.filter(item => {
                if (item.status === true) {
                    item.status = '未使用';
                } else {
                    item.status = '已失效';
                }
            });
        },
        methods: {
            detail(item) {
                this.$router.push({ name: 'noCardQueryListInfo', params: { id: item.id } });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('no-card-account');
                this.$ls.removeItem('no-card-list');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .cell-top {
        padding: 10px 0 10px 20px;
        font-size: 12px;
        font-weight: 600;
        background-color: #fff;
    }

    .no-data {
        position: relative;
        background-color: #fff;
        padding: 30px 20%;
        text-align: center;
        @include mini-border(top, #d9d9d9, after);
    }
</style>


