<template>
    <div>
        <div class="field-part">
            <mt-cell title="支付账户" @click.native="showAccount()" v-model="dataForm.account" isLink></mt-cell>
            <mt-cell title="账户余额:" v-model="dataForm.balance"></mt-cell>
            <mt-field label="预约金额:" placeholder="请输入100的整数倍" v-model="dataForm.order_money" type="number"></mt-field>
            <mt-field label="交易密码:" v-model="dataForm.password" :attr="{ maxlength: 6 }" type="text"></mt-field>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>
        <div class="field-action">
            <mt-button type="primary" size="small">返回</mt-button>
            <mt-button type="primary" size="small" @click="order">预约</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        data() {
            return {
                dataForm: {
                    account: '',
                    balance: '',
                    order_money: '',
                    password: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            bankName: '安徽农金',
                            balance: '200000.66'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            bankName: '安徽农金',
                            balance: '5000.27'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金',
                            balance: '600.34'
                        }
                    ]
                }
            };
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.dataForm.account = this.selectorPaymentAccount.checked.account;
                    this.dataForm.balance = this.selectorPaymentAccount.checked.balance + ' 元';
                }, (data) => {
                });
            },
            order() {
                let _data = Object.assign({}, this.dataForm);
                let money = parseFloat(_data.order_money) % 100;
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.order_money)) {
                    this.doMsg('请输入预约金额');
                    return false;
                }
                if (money !== 0) {
                    this.doMsg('请输入100的整数倍');
                    return false;
                }
                if (this.$validate.isEmpty(_data.password)) {
                    this.doMsg('请输入交易密码');
                    return false;
                }
                if (!this.$validate.isPwd(_data.password)) {
                    this.doMsg('密码格式错误');
                    return false;
                }
            },
            doMsg(msg, cl, duration) {
                Toast({
                    message: msg,
                    iconClass: cl || 'error',
                    duration: duration || '1000'
                });
            }
        }
    };

</script>


